export class PeopleTechComponent implements OnInit, OnDestroy {
  users:any = [];
  userFullName: string;
  userType: string;
  techType: string;
  _body = [];

  constructor(
    private userService: ApiCallsService,
    private storageService: StorageService,
    private dataService: DataService
  ) {}

  ngOnInit() {
    this.users = this.dataService.users;
    console.log('incoming users for user section : ' + JSON.stringify(this.users));
  }


  setBody() {
    this.storageService.getCardSubtitles().forEach(subtitle => {
      console.log("subs ret by ecbui from sesh store");
      console.log(subtitle);
    });
  }

  setTechType() {
    switch (this.userType) {
      case "FieldOfficer":
        this.techType = "Field Officer App";
        break;

      case "Admin":
        this.techType = "Admin Portal";
        break;

      default:
        this.techType = "null";
    }
  }

  ngOnDestroy() {
    // this.users = []
  }
}
